import { MAX_PHONE_NUMBER_LENGTH, NonEmptyString } from './BaseString';

export class PhoneNumber extends NonEmptyString<'PhoneNumber', string> {
  static from(value: string): PhoneNumber {
    return new PhoneNumber(value);
  }
  protected validate(value: string): string {
    value = super.validate(value);

    if (value.length > MAX_PHONE_NUMBER_LENGTH) {
      throw new Error(
        `PhoneNumber has maximum length of ${MAX_PHONE_NUMBER_LENGTH}`,
      );
    }

    return value;
  }
}
